@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,200;0,400;0,700;1,700&display=swap");

.input-section {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
.input-section > * {
  border: none;
  font-size: 1rem;
  font-family: "Josefin Sans";
  text-align: center;
}

.input {
  border-bottom: 0.1rem black solid;
  width: 80%;
}

.input:focus {
  outline: 0;
}
.adjust-input-select {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

@media screen and (max-width: 768px) {
  .adjust-input-select {
    display: flex;
    flex-direction: column;
  }
}
